.phonebook {
    width: 1200px;
    margin: 80px auto;
    padding: 20px;
    border: 1px solid #ddd;
    border-radius: 8px;
    color: #000000;

    background-image: url('/src/img/pexels-cottonbro-studio-4065906.jpg');
        background-size: cover;
        background-position: center bottom;
        /* положення фону по центру горизонталі та унизу вертикалі */
        background-repeat: no-repeat;
        background-attachment: fixed;
}

.title {
    font-size: 24px;
    margin-bottom: 15px;
    color: #000000;
    text-align: center;
}

